import { FC } from 'react'
import axios, { AxiosResponse } from 'axios'
import { useEffect, useState } from 'react'
import { Applicant } from 'src/models/Applicant'

import { useNavigate, useParams } from 'react-router-dom'
import { Box, Button, Container, Grid } from '@mui/material'
import SelectingTable from 'src/components/SelectingTable'
import BulkActions from './BulkActions'
import UserModal from './UserModal'
import { Form } from 'src/models/Form'
import { useAuthHeader } from 'react-auth-kit'
// import { Answers } from 'src/models/Answers'

const ApplyPage: FC<void> = () => {
  const [applicants, setApplicants] = useState<Applicant[]>([])
  const [selectedApplicants, setSelectedApplicants] = useState<number[]>([])
  const [questions, setQuestions] = useState<Form>({ question1: '', question2: '', question3: '' })
  const [isModal, setIsModal] = useState<boolean>(false)
  // const [answer, setAnswer] = useState<Answers>({ answer1: '', answer2: '', answer3: '' })
  const [clickedApplicant, setClickedApplicant] = useState<number>(0)
  const authHeader = useAuthHeader()
  const navigate = useNavigate()
  const properties = [
    { header: '이름', property: 'name' },
    { header: '학번', property: 'studentId' },
    { header: '학과', property: 'department' },
    { header: '연락처', property: 'phoneNumber' },
    { header: '이메일', property: 'email' }
  ]

  const params = useParams()

  const handleItemClick = (idx) => {
    setClickedApplicant(idx)
    setIsModal(true)
  }

  useEffect(() => {
    axios
      .get(`${process.env.REACT_APP_APIHOST}/apply/applicants/${params.id}`, {
        headers: { Authorization: authHeader() }
      })
      .then((res: AxiosResponse<Applicant[]>) => {
        setApplicants(res.data)
      })

    axios.get(`${process.env.REACT_APP_APIHOST}/club/form?clubId=${params.id}`).then((res: AxiosResponse<Form>) => {
      setQuestions(res.data)
    })
  }, [])

  return (
    <Container
      maxWidth="md"
      sx={{
        height: '80vh',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'end',
        alignItems: 'center'
      }}
    >
      <Grid container direction="row" justifyContent="center" alignItems="stretch" spacing={3}>
        <Grid item xs={12}>
          <SelectingTable
            items={applicants}
            title="지원자 목록"
            selectedItems={selectedApplicants}
            setSelectedItems={setSelectedApplicants}
            BulkActions={<BulkActions selectedApplicants={selectedApplicants} />}
            properties={properties}
            handleClick={handleItemClick}
          />
        </Grid>
      </Grid>
      <Box m={2}>
        <Button
          variant="outlined"
          onClick={() => {
            navigate(-1)
          }}
        >
          뒤로 가기
        </Button>
      </Box>

      <UserModal
        open={isModal}
        onClose={() => setIsModal(false)}
        questions={questions}
        Applicants={applicants}
        current={clickedApplicant}
      />
    </Container>
  )
}

export default ApplyPage
